<template>
    <div class="person">
        <img class="img" src="../assets/05.jpg" width="100%">
        <div class="person_content">
            <div class="menu">
                <Menu theme="light" :accordion='true' @on-select="handleSelect">
                    <Submenu name="1">
                        <template slot="title">
                            
                        </template>
                        <!-- <MenuItem name="1-1"></MenuItem> -->
                        <MenuItem name="deleteOrder"></MenuItem>
                        <MenuItem name="confirmUserOrder">()</MenuItem>                        
                        <!-- <MenuItem name="1-3"></MenuItem> -->
                        <MenuItem name="orderHistory"></MenuItem>
                        <MenuItem name="confirmOrder">()</MenuItem>
                    </Submenu>
                    <Submenu name="2">
                        <template slot="title">
                            
                        </template>
                        <MenuItem name="newReport"></MenuItem>
                        <MenuItem name="cancelReport"></MenuItem>
                        <MenuItem name="reportHistory"></MenuItem>
                    </Submenu>
                    <!-- <Submenu name="2">
                        <template slot="title">
                            
                        </template>
                        <MenuItem name="personData"></MenuItem>
                        <!- <MenuItem name="2-2"></MenuItem> ->
                    </Submenu> -->
                        <MenuItem name="personData"></MenuItem>
                        <MenuItem name="collection"></MenuItem>
                    
                    <Submenu name="3">
                        <template slot="title">
                            
                        </template>
                        <MenuItem name="publishInfo" ></MenuItem>
                        <MenuItem name="modifyPublish"></MenuItem>
                        <MenuItem name="deletePublish"></MenuItem>
                        <!-- <MenuItem name="publishHistory"></MenuItem> -->
                    </Submenu>
                </Menu>
            </div>

            <div class="view">
                <keep-alive>
                    <router-view></router-view>
                </keep-alive>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            
        }
    },
    methods: {
        handleSelect(cvalue){
            this.$router.push({path: cvalue});
        }
    }
}
</script>

<style lang="less" scoped>

    .person{
        .img{
            min-width: 1400px;
        }
        .person_content{
            // margin-top: 30px;
            overflow: hidden;
            width: 1100px;
            height: 800px;
            margin: 30px auto 0 auto;
            // padding: 35px;
            div{
                display: inline-block;         
                float: left;     
                // border: 1px solid #ddd;                  
            }
            .view{
                margin-left: 15px;
                padding: 30px 40px;
                height: 800px;
                background: #f3f3f3;
                width: 840px;
                float: left;
            }
        }
    }

</style>



